.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: space-around;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.App-link {
  color: #09d3ac;
}

.App-img {
  margin: 25px;
  position: absolute;
  top: 30px;
  width: 338px;
  position: fixed;
  top: 0;
}
